#burger-menu {
  z-index: 2;
  cursor: pointer;
  position: absolute;
  top: 3%;
  right: 5%;
  overflow: visible;
}

#burger-menu span, #burger-menu span:before, #burger-menu span:after {
  height: 4px;
  opacity: 1;
  background: #fae6e6;
  transition: all .3s ease-in-out;
  display: block;
  position: absolute;
}

#burger-menu span:before, #burger-menu span:after {
  content: "";
}

#burger-menu span {
  width: 27px;
  border-radius: 25%;
  top: 13px;
  right: 0;
}

#burger-menu span:before {
  width: 27px;
  border-radius: 25%;
  top: -10px;
  left: 0;
}

#burger-menu span:after {
  width: 27px;
  border-radius: 25%;
  top: 10px;
  left: 0;
}

#burger-menu.close span {
  width: 27px;
  top: 13px;
  transform: rotate(-45deg);
}

#burger-menu.close span:before {
  width: 27px;
  top: 0;
  transform: rotate(90deg);
}

#burger-menu.close span:after {
  opacity: 0;
  width: 0;
  top: 0;
  left: 0;
  transform: rotate(90deg);
}

